<template>
<div>
  <Row :gutter="30" type="flex" align="middle">
    <Col :md="24" :lg="9">
        <Row type="flex" align="middle">
            <Col span="18">
                <Input v-model="value_number" placeholder="号码格式01 02 07 09 10" maxlength="20" size="large">
                    <Select v-model="value_number_select" slot="append" style="width: 66px">
                        <Option value="m0">M0</Option>
                        <Option value="m1">M1</Option>
                        <Option value="m2">M2</Option>
                        <Option value="m3">M3</Option>
                        <Option value="m4">M4</Option>
                    </Select>
                </Input>
            </Col>
            <Col span="6">
                <Button type="primary" :loading="loading" @click="toLoading" :style="{width: '80px', margin:'5px'}">
                    <span v-if="!loading">执行</span>
                    <span v-else>加载中</span>
                </Button>
            </Col>
        </Row>
    </Col>
    <Col :md="24" :lg="15">
          <Button type="primary" @click="sxDialog = true">筛选</Button>
          <Modal
              v-model="sxDialog"
              title="筛选条件"
              @on-ok="ok"
              @on-cancel="cancel">
              <p>Content of dialog</p>
              <p>Content of dialog</p>
              <p>Content of dialog</p>
          </Modal>
    </Col>
  </Row>
  <br>
  <Row :gutter="30">
    <Col :md="8" :lg="9">
        <Card :style="{padding: '0px, 0px'}">
            <p slot="title">组选号码</p>
            <Input v-model="value_numbers" :disabled="disabled" type="textarea" :rows="17"
            placeholder="预测号码" readonly=true :style="{width: '100%'}"></Input>
            </br>
            <p :style="{marginTop:'10px', textAlign: 'center'}">共{{zhuNum}}组</p>
          </Card>
    </Col>
    <Col :md="16" :lg="15">
        <p class="card-title" :style="{}">
            <Icon type="android-list"></Icon>
            今日开奖列表
        </p>
        <div :style="{marginTop:'10px'}">
            <Table border stripe :loading="tableLoading" height="444" :columns="tableColumns"
            :data="tableData" :class="getTableData"
            :row-class-name="tableRowClassName"></Table>
        </div>
    </Col>
  </Row>
</div>
</template>

<script>
import http from '../utils/http'
import util from '../utils/util'

export default {
  data () {
    return {
      loading: false,
      value_number_select: 'm2',
      sxDialog: false,
      tableLoading: true,
      zhuNum: '0',
      tableColumns: [
        {
          title: '期号',
          key: 'data_period',
          align: 'center',
          sortable: true,
          sortType: 'desc',
          fixed: 'left',
          width: 100
        },
        {
          title: '开奖号码',
          align: 'center',
          key: 'data_award',
          width: 160
        },
        {
          title: '类型',
          align: 'center',
          key: 'data_type',
          width: 100
        },
        {
          title: '大小比',
          align: 'center',
          key: 'data_size',
          width: 100
        },
        {
          title: '奇偶比',
          align: 'center',
          key: 'data_qiou',
          width: 100
        },
        {
          title: '质合比',
          align: 'center',
          key: 'data_zhihe',
          width: 100
        }
      ],
      tableData: []
    }
  },
  computed: {

  },
  mounted: function () {
    this.getOpenData()
    setInterval(this.getOpenData, 30000)
  },
  methods: {
    ok () {
      this.$Message.info('Clicked ok')
    },
    cancel () {
      this.$Message.info('Clicked cancel')
    },
    toLoading: async function () {
      this.loading = true
      let number = this.value_number
      let re = /^(\d{2}\s)+\d{2}$/
      if (!re.test(number)) {
        this.$Message.warning('格式不正确，例如01 02 03 04 05')
      } else {
        this.loading = true
        let params = {
          type: this.value_number_select,
          numbers: number.replace(/ /g, ',')
        }
        const res = await http.post('/lottery', params)
        if (http.isSuccess) {
          this.loading = false
          let listData = res.listData
          let content = ''
          let index = 0
          for (let i = 0; i < listData.length; i++) {
            if (index > 0) {
              content = content + '\n'
            }
            content = content + listData[i].number
            index++
          }
          this.zhuNum = index
          this.value_numbers = content
          // this.disabled = true
        }
      }
      this.loading = false
    },
    getOpenData: async function () {
      let date = util.dataFormat(new Date(), 'yyyyMMdd')
      this.tableLoading = true
      let params = {
        date: date.substring(2)
      }
      const res = await http.post('/lottery/getOpenData', params)
      if (http.isSuccess) {
        let listData = res.listData
        const data = []
        for (let i = 0; i < listData.length; i++) {
          let type = listData[i].data_type
          let period = listData[i].data_period
          let award = listData[i].data_award
          let size = listData[i].data_size
          let qiou = listData[i].data_qiou
          let zhihe = listData[i].data_zhihe
          if (type === 'M1') {
            data.push({
              data_period: period,
              data_award: award,
              data_type: type,
              data_size: size,
              data_qiou: qiou,
              data_zhihe: zhihe,
              cellClassName: {
                data_type: 'table-info-cell-type1'
              }
            })
          } else if (type === 'M2') {
            data.push({
              data_period: period,
              data_award: award,
              data_type: type,
              data_size: size,
              data_qiou: qiou,
              data_zhihe: zhihe,
              cellClassName: {
                data_type: 'table-info-cell-type2'
              }
            })
          } else if (type === 'M3') {
            data.push({
              data_period: period,
              data_award: award,
              data_type: type,
              data_size: size,
              data_qiou: qiou,
              data_zhihe: zhihe,
              cellClassName: {
                data_type: 'table-info-cell-type3'
              }
            })
          } else if (type === 'M4') {
            data.push({
              data_period: period,
              data_award: award,
              data_type: type,
              data_size: size,
              data_qiou: qiou,
              data_zhihe: zhihe,
              cellClassName: {
                data_type: 'table-info-cell-type4'
              }
            })
          } else {
            data.push({
              data_period: period,
              data_award: award,
              data_type: type,
              data_size: size,
              data_qiou: qiou,
              data_zhihe: zhihe
            })
          }
        }
        this.tableData = data
        this.tableLoading = false
      }
    }

    // tableRowClassName (row, index) {
    //   if (row.data_type === 'M1') {
    //     console.log(row.data_type + ' ' + index)
    //   }
    // }
  }
}
</script>

<style>
  input:focus, textarea:focus {
      outline: none;
  }
  textarea {
    resize: none;
  }
  .card-title {
    color: #abafbd;
    font-size: 20px;
  }
  .ivu-table .table-info-cell-type1 {
    color: rgb(3, 160, 250);
    font-weight: bold;
  }
  .ivu-table .table-info-cell-type2 {
    color: rgb(243, 18, 18);
    font-weight: bold;
  }
  .ivu-table .table-info-cell-type3 {
    color: rgb(50, 167, 35);
    font-weight: bold;
  }
  .ivu-table .table-info-cell-type4 {
    color: rgb(210, 17, 228);
    font-weight: bold;
  }
</style>



